import axios from "../../../shared/axios.js";
export default {
  props: {
    prop: {
      type: Object,
      required: true
    },
    commonPerformedWorks: {
      type: Array,
      required: true
    },
    masters: {
      type: Array,
      required: true
    }
  },
  emits: ["submit:error", "submit:success"],

  data() {
    return {
      form: {
        id: this.prop.row._id,
        technician_id: this.prop.row.technician_id,
        performed_works: this.prop.row.performed_works
      },
      rules: {
        technician_id: [{
          required: true,
          message: "Пожалуйста, укажите мастера",
          trigger: "blur"
        }],
        performed_works: [{
          required: true,
          message: "Пожалуйста, укажите проделанные работы",
          trigger: "blur"
        }]
      }
    };
  },

  methods: {
    sendData() {
      axios.patch("/dashboard/table", this.form).then(() => {
        this.$emit("submit:success", this._id);
      }).catch(() => {
        this.$emit("submit:error");
      });
    },

    submitForm() {
      this.$refs.formRef.validate(valid => {
        if (valid) {
          this.sendData();
          return true;
        }

        return false;
      });
    }

  }
};